import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        wordData: null
    },
    getters: {
        words({ wordData }) {
            return wordData ? wordData.words : null
        }
    },
    mutations: {
        setWordData(state, data) {
            state.wordData = data
        }
    },
    actions: {
        getWordsData({ commit }, path) {
            axios
                .get(path)
                .then(res => {
                    commit('setWordData', res.data)
                })
                .catch(err => {
                    console.log(err.response)
                    alert('載入失敗')
                })
        }
    }
})



