<template>
    <v-ons-page>
        <section v-if="words">
            <!-- <v-ons-input modifier="material" placeholder="Input word" float v-model="word"></v-ons-input> -->
            <div class="card">
                <div>{{word.word}}</div>
            </div>
            <div class="explain" v-show="explainShow">{{word.explain}}</div>
            <v-ons-button class="nextbn" modifier="large" @click="next" v-show="canNext">下一個</v-ons-button>
        </section>
    </v-ons-page>
</template>

<script>
import rootDelegate from '@/delegate/rootDelegate'
export default {
    name: 'HomePage',
    mixins: [rootDelegate],
    data() {
        return {
            wordKey: -1,
            explainShow: false,
            canNext: false
        }
    },
    computed: {
        word() {
            return this.words ? this.words[this.wordKey] : null
        }
    },
    methods: {
        next() {
            this.wordKey =
                this.wordKey === this.words.length - 1 ? 0 : this.wordKey + 1
        },
        playSound() {
            let u = this.u
            u.text = this.word.word
            u.lang = 'ja-JP' // So system knows the right voice to use
            u.rate = 0.8
            window.speechSynthesis.speak(u)
        }
    },
    watch: {
        words(val) {
            if (val) {
                this.wordKey = 0
                this.playSound()
            }
        },
        wordKey(val) {
            this.canNext = false
            this.explainShow = false
            this.playSound()
        }
    },
    mounted() {
        this.u = new SpeechSynthesisUtterance('')
        this.u.onend = () => {
            this.explainShow = true
            this.canNext = true
        }
        this.getWordsData('./static/word.json')
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
section {
    padding: 20px;
}
.card {
    text-align: center;
    font-size: 1.2em;
}
.explain {
    padding: 20px;
    text-align: center;
}
.nextbn {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: calc(100% - 40px);
    margin: 20px;
}
</style>



